@font-face {
  font-family: 'Apercu';
  src: url("~static/fonts/Apercu-Regular.otf") format("opentype");
}
@font-face {
  font-family: 'Apercu-Medium';
  src: url("~static/fonts/Apercu-Medium.otf") format("opentype");
}

body {
  margin: 0;
  font-family: "Apercu", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 1em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
