@import "styles/variables.scss";

.preScreen {
  max-width: $mobile-max-width;
  width: 90vw;
  height: 100vh;
  display: flex;
  flex-direction: column;

  :global {
    .finished {
      line-height: 100vh;
      text-align: center;
    }

    .frame {
      flex: 1;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .main-button {
      align-self: stretch;
      margin: {
        left: 50px;
        right: 50px;
        bottom: 30px;
      }
    }

    .text {
      font-size: 23px;
      color: #635f76;
      line-height: 30px;
      text-align: center;
    }

    .green {
      @extend %medium;
      color: #41b4b4;
    }

    .yellow {
      @extend %medium;
      color: #e7812e;
    }
  }
}

.invisible {
  display: none;
}
